import {
  SOCKET_LOADING,
  SOCKET_START,
  SOCKET_UPDATE ,
  SOCKET_DISCONNECT ,
  SOCKET_FAIL 

} from './Types'

export const setSocketStart = (data) => {
  return (dispatch) => {
    try {
      dispatch(socketLoading(true))
      dispatch(socketStart(data))
      dispatch(socketLoading(false))

    } catch (error) {
      dispatch(socketLoading(false))
      dispatch(socketFail(error))
    }
  }
}
export const setDisconnect = () => {
  return (dispatch) => {
    try {
      dispatch(socketDisconnect())
      dispatch(socketLoading(false))
    } catch (error) {
      dispatch(socketLoading(false))
      dispatch(socketFail(error))
    }
  }
}

export const socketDisconnect = () => {
  return {
    type: SOCKET_DISCONNECT,
  }
}
export const socketLoading = (data) => {
  return {
    type: SOCKET_LOADING,
    payload: data,
  }
}
export const socketStart = (data) => {
  return {
    type: SOCKET_START,
    payload: data,
  }
}
export const socketFail = (data) => {
  return {
    type: SOCKET_FAIL,
    payload: data,
  }
}
