import React, { useState, useEffect } from 'react';
import { connect } from "react-redux";
import { setLogin, setConfig, setBoard, setGroup, setLogout,  setSocketStart , setDisconnect  } from "./redux/index";

import { POST_MENU} from './common';
import { useSession } from "./lib/auth";


import { Route } from 'react-router-dom';
import { CSSTransition } from 'react-transition-group';


import {useSocket} from './socket/index'


import 'bootstrap/dist/css/bootstrap.css'
import 'primereact/resources/themes/bootstrap4-light-purple/theme.css'
import 'primereact/resources/primereact.min.css';
import 'primeicons/primeicons.css';
import 'primeflex/primeflex.css';
import 'prismjs/themes/prism-coy.css';
import '@icon/icofont/icofont.css'
import './App.css'
import Main from './pages/Main';
import Login from './pages/Login';
import Register from './pages/Register';
import About from './pages/About';
import Contact from './pages/Contact';
import Skill from './pages/Skill';
import Portfolio from './pages/Portfolio';
import { useCheck } from './lib/check';
import Loading from './components/Loading';
import AppMenu from './layout/AppMenu';


const App = ({
  authData,
  setConfig,
  setBoard,
  setGroup,
  setLogin,

  socketData,messageData,
  setSocketStart, setDisconnect 
}) => {



  const [isStart, setIsStart] = useState(false)
  const [isLoading, setIsLoading] = useState(false)


  useSession(setLogin, socketData);
  useCheck(setIsLoading,setConfig, setBoard,setGroup, setIsStart)
  // useSocket(setSocketStart , setDisconnect , setBoard, setConfig, setGroup, setIsStart)
  useSocket(setSocketStart , setDisconnect  )



  return (
    <div id="layout-wrapper" className="layout-wrapper">
      <Loading isLoading={isLoading} />
      {isStart ? (
        <div>
          {/* <AppMenu /> */}
        <AppMenu/>
          <div className="layout-main-container">
            <Route path="/" exact component={Main} />
            <Route path="/auth/login" component={Login} />
            <Route path="/auth/register" component={Register} />
            <Route path="/about" component={About} />
            <Route path="/contact" component={Contact} />
            <Route path="/skill" component={Skill} />
            <Route path="/portfolio" component={Portfolio} />

          {/* <AppFooter /> */}
          </div>
          
        </div>
      ) : null}
    </div>
  );
}

const mapStateToProps = (state) => {
  return {
    configData: state.configData,
    authData: state.authData,

    socketData:state.socketData,
    messageData:state.messageData
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
      setLogout:(data)=>dispatch(setLogout(data)),
    setLogin: (data) => dispatch(setLogin(data)),
    setConfig: (data) => dispatch(setConfig(data)),
    setBoard: (data) => dispatch(setBoard(data)),
    setGroup: (data) => dispatch(setGroup(data)),

    setSocketStart : (data)=> dispatch(setSocketStart(data)),
    setDisconnect : ()=> dispatch(setDisconnect())
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(App);


