import React from "react";
import { connect } from "react-redux";
import { Button } from 'primereact/button';

import { setLogin, setConfig, setBoard, setGroup, setLogout } from "../redux/index";
import { goToScroll } from "../lib";

const Main = ({ authData, configData }) => {
   
    return (
        <section id="section-header" className="section-show">
          
        </section>
    );
};
const mapStateToProps = (state) => {
    return {
        configData: state.configData,
        authData: state.authData,
    };
};
const mapDispatchToProps = (dispatch) => {
    return {
        setLogout: (data) => dispatch(setLogout(data)),
        setLogin: (data) => dispatch(setLogin(data)),
        setConfig: (data) => dispatch(setConfig(data)),
        setBoard: (data) => dispatch(setBoard(data)),
        setGroup: (data) => dispatch(setGroup(data)),
    };
};
export default connect(mapStateToProps, mapDispatchToProps)(Main);

