import React from "react";
import { ProgressBar } from 'primereact/progressbar';
const Skill = () => {
  const skillItems=[
    {label:'NodeJs',type:'Software platform',level:80,children:[
      {label:'express',type:'library',level:90,children:[]},
      {label:'babel',type:'library',level:90,children:[
        {label:'babel-cli',type:'library',level:90,children:[]},
      ]},
    ]},
    {label:'Html',type:'language',level:90,children:[]},

    {label:'Javascript',type:'language',level:90,children:[
      {label:'socket.io',type:'library',level:99,children:[
        {label:'socket.io-client',type:'library',level:99,children:[]},
      ]},
      {label:'redux',type:'library',level:99,children:[
        {label:'react-redux',type:'library',level:99,children:[]},
      ]},
      {label:'React',type:'framework',level:80,children:[
        {label:'reactStrap',type:'library',level:99,children:[]},
        {label:'primeReact',type:'library',level:99,children:[]},
      ]},
    ]},

    {label:'CSS',type:'language',level:70,children:[]},
    {label:'SASS',type:'language',level:70,children:[]},
   
    {label:'MySql',type:'language',level:70,children:[
     {label:'Sequelize',type:'owl',level:70,children:[]}
    ]},
  ]
  return (
    <section id="skill" className="skill">
   
      <div className="container-fluid">
        <div className="section-title">
        <h2>Skill</h2>
          <p>Here are my skills and usage examples.</p>
          <div style={{width:"500px"}}>
            {skillItems.map((item,i)=>{
              return(
                <SkillTree  key={i} item={item} index={i}/>
               
              )
            })}
          </div>
        </div>
      </div>
    </section>
  );
};

export default Skill;


const SkillTree = ({item ,index}) => {
    return (
      <ul>
        <li>
          <div className="">{item.label}</div>
          <div className="">
            <ProgressBar showValue={false} value={item.level}></ProgressBar>
            {item.level}%
          </div>
          {item.children.length > 0
          ? item.children.map((children, i) => (
              
                <SkillTree key={i} item={children} index={i} />
            ))
          : null}
        </li>
       
      </ul>
    );
}