import moment from 'moment'
import {
  SOCKET_LOADING,
  SOCKET_START,
  SOCKET_UPDATE ,
  SOCKET_DISCONNECT ,
  SOCKET_FAIL 

} from './Types'
const initialState = {
  isLoading: false, 
  failData: null, 
  isSocket:false,


  socket:null
}
const reducer = (state = initialState, action) => {
  switch (action.type) {
    case SOCKET_LOADING:
      return {
        ...state,
        isLoading: action.payload,
      }
    case SOCKET_START:
      return {
        ...state,
        isSocket:true,
        socket: action.payload,
        startDate:moment().format('YYYY-MM-DD h:mm:ss')
      }
    case SOCKET_DISCONNECT:
      return initialState
    case SOCKET_FAIL:
      return {
        ...state,
        failData: action.payload,
      }
    default: return state
  }
}

export default reducer
