import {
  MESSAGE_LOADING,
  MESSAGE_UPDATE,
  MESSAGE_FAIL
} from './Types'



const initialState = {
  isLoading: false, 
  failData: null, 
  message:[]

  
}

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case MESSAGE_LOADING:
      return {
        ...state,
        isLoading: action.payload,
      }
    case MESSAGE_UPDATE:
      return {
        ...state,
        message: action.payload,
      }
    case MESSAGE_FAIL:
      return {
        ...state,
        failData: action.payload,
      }
    default: return state
  }
}

export default reducer
