import { useEffect } from "react";
import {postApi} from '../api'
import { CONFIG } from "../common";
import moment from 'moment'

export const useCheck = (loadingFunction,setConfig, setBoard,setGroup ,setIsStart) => {
   useEffect(() => {
    postApi(loadingFunction, CONFIG, (res) => {
 
      setConfig(res.data.config)
      setBoard(res.data.boardConfig)
      setGroup(res.data.groupConfig)
      setIsStart(true)
      })
   }, [loadingFunction, setBoard, setConfig, setGroup, setIsStart])

}

export const onedayCheck = (time) => {
  const nowTime = moment(); // 
  const createdTime = moment(time);
  const resultTime = moment.duration(nowTime.diff(createdTime)).asDays(); // 
  if (resultTime < 0) {
    return false
  } else {
    return true
  }
};