import {useEffect} from 'react'
import io from 'socket.io-client'
import { PUBLIC_URL } from '../common';
let socket;

export const useSocket= (
  setSocketStart ,
  setDisconnect,
  
  )=>{
  const ENDPORINT =PUBLIC_URL
 

    useEffect(() => {
      socket = io(ENDPORINT);
      


      socket.emit("start",{data:'hi'}, (res) => {
       
        setSocketStart(socket)
      });
      
  
      
      socket.on("disconnect", () => {
        setDisconnect()
      });
       
    }, [ENDPORINT, setDisconnect, setSocketStart]);
 
  }