import React, { useState } from "react";
import { connect } from "react-redux";
import { setLogin} from "../redux/index";
import { InputText } from "primereact/inputtext";
import { Button } from "primereact/button";
import { Password } from "primereact/password";
import { Dialog } from "primereact/dialog";
import {Checkbox} from "primereact/checkbox"
import { GOOGEL_LOGIN, LOGIN, REMEMBER_NAME, SOCIAL_LOGIN, TOKEN_NAME } from "../common";
import { useHistory } from "react-router-dom";
import { postApi } from "../api";
import PropTypes  from 'prop-types'
import GoogleLogin from "react-google-login";
import KaKaoLogin from 'react-kakao-login'
import NaverLogin from 'react-naver-login';
const Login = ({ setLogin ,loginHandler, configData, socketData }) => {


    const [showMessage, setShowMessage] = useState(false);
    const loginInit = {
        userId: "",
        password: "",
        remember: true,
    };
    const [formData, setFormData] = useState(loginInit);
    const [isLoading, setIsLoading] = useState(false)
    const history=useHistory()
  
    const inputChangeHandler=(e)=>{
        setFormData({
            ...formData,
            [e.target.id]:e.target.value
        })
    }
    const onSubmitHandler=(e)=>{
        e.preventDefault()
        socketData.socket.emit('local-login',formData,(req)=>{
            console.log(req)
            if (req.status) {
                        setLogin(req.data);
                        localStorage.setItem(TOKEN_NAME, req.token);
                        localStorage.setItem(REMEMBER_NAME, formData.remember);
                        history.push('/')
                        loginHandler()
                      } else {
                        setShowMessage(!showMessage)
                      }
        })
        // postApi(
        //     setIsLoading,
        //     LOGIN,
        //     (req) => {
        //       if (req.data.status) {
        //         setLogin(req.data.data);
        //         localStorage.setItem(TOKEN_NAME, req.data.token);
        //         localStorage.setItem(REMEMBER_NAME, formData.remember);
        //         history.push('/')
        //         loginHandler()
        //       } else {
        //         setShowMessage(!showMessage)
        //       }
        //     },
        //     formData
        //   );

    }
    const socialLoginHandler = (data) => {
      socketData.socket.emit('google-login',data)
    }

    return (
        <form onSubmit={onSubmitHandler}>
            <div className="flex align-items-center justify-content-center">
                <div className="surface-card p-4 shadow-2 border-round w-full lg:w-6">
                    <div className="text-center mb-5">
                        {/* <img src="assets/images/blocks/logos/hyper.svg" alt="hyper" height={50} className="mb-3" /> */}
                        <div className="text-900 text-3xl font-medium mb-3">Welcome Admin</div>
                    </div>

                    <div>
                        <label htmlFor="email" className="block text-900 font-medium mb-2">
                            Email
                        </label>
                        <InputText onChange={inputChangeHandler} name="email" id="userId" className="w-full mb-3" />
                        <label htmlFor="password" className="block text-900 font-medium mb-2">
                            Password
                        </label>
                        <InputText type="password" id="password" className="w-full mb-3" onChange={inputChangeHandler} />

                        <div className="flex align-items-center justify-content-between mb-6">
                            <div className="flex align-items-center">
                                <Checkbox
                                    className="mr-2"
                                    onChange={(e) => {
                                        setFormData({ ...formData, remember: e.checked });
                                    }}
                                    id="remember"
                                    checked={formData.remember}
                                />
                                <label htmlFor="rememberme">Remember me</label>
                            </div>
                        </div>

                        <Button label="Local Login" type="submit" icon="pi pi-user" className="w-full mb-1" />

                        <hr />
                        {configData.dc_auth_google.isUse ? (
                            <GoogleLogin
                                clientId={configData.dc_auth_google.publicKey}
                                onSuccess={(e) => {
                                    console.log(e.profileObj);
                                    console.log(e.accessToken);
                                    socialLoginHandler({
                                        id: e.googleId,
                                        type: "google-login",
                                        loginData: e.profileObj,
                                        token: e.accessToken,
                                    });
                                }}
                                onFailure={(f) => {
                                    console.log(f);
                                }}
                                disabled={false}
                                cookiePolicy={"single_host_origin"}
                            />
                        ) : null}
                        {configData.dc_auth_kakao.isUse ? (
                            <KaKaoLogin
                                token={configData.dc_auth_kakao.publicKey}
                                onSuccess={(e) => { 
                                    socialLoginHandler({
                                        id: e.profile.id,
                                        type: "kakao-login",
                                        loginData: e.profile.kakao_account,
                                        token: e.response.access_token,
                                    });
                                }}
                                onFail={(e) => {
                                    console.log(e);
                                }}
                                onLogout={(e) => {
                                    console.log(e);
                                }}
                            />
                        ) : null}
                        {configData.dc_auth_naver.isUse ? (
                            <NaverLogin 
                            clientId={configData.dc_auth_naver.publicKey} 
                            callbackUrl="http://localhost:3001" 
                            render={(props) => <button onClick={ props.onClick}>Naver Login</button>} 
                            onSuccess={(naverUser) => console.log(naverUser)} 
                            onFailure={(result) => console.log(result)} />
                        ) : null}
                    </div>
                </div>
            </div>
        </form>
    );
};
const mapStateToProps = (state) => {
    return {
      authData: state.authData,
      configData:state.configData,
      socketData:state.socketData
    };
  };
  
  const mapDispatchToProps = (dispatch) => {
    return {
      setLogin: (data) => dispatch(setLogin(data))
    };
  };
  
  export default connect(mapStateToProps, mapDispatchToProps)(Login);
  
  Login.propTypes = {
    loginHandler: PropTypes.func
  };
  Login.defaultProps= {
    loginHandler: ()=>{}
  };