import {
  AUTH_LOADING,
  AUTH_UPDATE,
  AUTH_LOGOUT,
  AUTH_FAIL,

} from './Types'
import {TOKEN_NAME, REMEMBER_NAME} from '../../common'
const initialState = {
  isLoading: true, 
  isStart:false,
  failData: null, 
  isLogin: false,
  isAdmin:false,
  remember:false,
  userId:'', 
  email:'',
  firstName:'',
  lastName:'',
  fullName:'',
  nickName:'',
  useName:'',
  userImage:'/basic/default/user.jpg',
  userToken:'',
  userData: {},
  created:'',
  updated:'',
  level:0,
  grade:0,
  mobile:'',
  iat:0,
  exp:0,
  age:'',
  address: {},
  birthday:'',  
}

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case AUTH_LOADING:
      return {
        ...state,
        isLoading: action.payload,
      }
    case AUTH_UPDATE:
      return {
        ...state,
        isStart:true,
        isLogin: true,
        isAdmin:action.payload.isAdmin,
        remember:action.payload.remember,
        userId:action.payload.userId,
        userToken:action.payload.userToken,
        userData: action.payload.userData,
        created:action.payload.created,
        updated:action.payload.updatedAt,
        email:action.payload.email,
        firstName:action.payload.firstName,
        lastName:action.payload.lastName,
        fullName:action.payload.fullName,
        nickName:action.payload.nickName,
        useName: action.payload.useName,
        userImage: action.payload.userImage,
        type: action.payload.type,
        level: action.payload.level,
        grade: action.payload.grade,
        mobile: action.payload.mobile,
        iat: action.payload.iat,
        exp: action.payload.exp,
        age: action.payload.age,
        address:  action.payload.address,
        birthday: action.payload.birthday,
      }
    case AUTH_LOGOUT:
      localStorage.removeItem(TOKEN_NAME);
      localStorage.removeItem(REMEMBER_NAME);
      return initialState
    case AUTH_FAIL:
      return {
        ...state,
        failData: action.payload,
      }

    default: return state
  }
}

export default reducer
