import React, { useState } from 'react'
import './AppMenu.css'
import Icofont from 'react-icofont'
import {Link} from 'react-router-dom'

const AppMenu = () => {
    const [active, setActive] = useState(0)
    const items=[
        {label:'Home',to:"header"},
        {label:'About',to:"about"},
        {label:'Skill',to:"skill"},
        {label:'Portfolio',to:"portfolio"},
        {label:'Contact',to:"contact"},
    ]
    const Linklist=()=>{

        return (
            <ul>
                {items.map((item,i)=><li key={i} className={active===i?'active':''}><Link to={`/${item.to}`}  onClick={()=>{onClickLink(item.to , i)}}>{item.label}</Link></li>)}
            </ul>
        )
    }
    const onClickMobileBar = (e) => {
        e.preventDefault()
        const bodyEl = document.getElementById("body")
        bodyEl.classList.toggle('mobile-nav-active')
    }
const onClickLink = (id,i) => {
   setActive(i)

    const colors=["#0000003b","#5403326c","#2903546c","#0326546c","#0351546c"]
    document.getElementById("layout-wrapper").style=`background-color:${colors[Math.floor(Math.random() * 4)]}`

    if(id==='header' || id==="contact"){
      document.getElementById("layout-wrapper").classList.add('image')
    }else{
      document.getElementById("layout-wrapper").classList.remove('image')
    }

    if(id==='header'){
        document.getElementById("header").classList.remove("header-top")
       
    }else{
        document.getElementById("header").classList.add("header-top")
        // document.getElementById("layout-wrapper").classList.add(`section-show`)
       
        setTimeout(() => {
          document.getElementById(id).classList.add("section-show")
         
        }, 500);
       
    }
}

    return (
      <div>
        <header id="header" className="header-tops">
          <div className="container">
            <h1>
              <Link
                to="/header"
                onClick={() => {
                  onClickLink("header", 0);
                }}
              >
                Jung-hyun Gwon
              </Link>
            </h1>
            <h2>
              I'm a passionate <span>Progremer</span> from Republic of Korea
            </h2>
            <nav className="nav-menu d-none d-lg-block">
              <ul>
                <Linklist />
              </ul>
            </nav>
          </div>
        </header>
        <div>
          <button
            type="button"
            className="mobile-nav-toggle d-lg-none"
            onClick={onClickMobileBar}
          >
            <Icofont icon="navigation-menu" />
          </button>
          <nav className="mobile-nav d-lg-none">
            <ul>
              <Linklist />
            </ul>
          </nav>
        </div>
      </div>
    );
}

export default AppMenu
